import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import ReactDOM from 'react-dom'
//import {cat1, cat2, cat3, cat4, cat5, cat6, cat7, cat8, cat9, cat10, cat11, cat12} from './'
import cat1 from './cat1.jpeg'
import cat2 from './cat2.jpeg'
import cat3 from './cat3.jpeg'
import cat4 from './cat4.jpeg'
import cat5 from './cat5.jpeg'
import cat6 from './cat6.jpeg'
import cat7 from './cat7.jpeg'
import cat8 from './cat8.jpeg'
import cat9 from './cat9.jpeg'
import cat10 from './cat10.jpeg'
import cat11 from './cat11.jpeg'
import cat12 from './cat12.jpeg'




class Square extends React.Component {
  render() {
    return (
      <img src={this.props.value}
        onClick={() => this.props.onClick()}
      />
    );
  }
}

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      squares: [cat1, cat2, cat3, cat4, cat5, cat6, cat7, cat8, cat9, cat10, cat11, cat12],
      selectedCats: [],
      score: 0,
      maxScore: 0
    };
  }


  shuffle(a) {
    var j, x, i;
    for (i = a.length - 1; i > 0; i--) {
      j = Math.floor(Math.random() * (i + 1));
      x = a[i];
      a[i] = a[j];
      a[j] = x;
    }
    return a;
  }

  handleClick(i) {
    const squares = this.state.squares.slice();
    if (!this.state.selectedCats.includes(i)) {
      const selected = this.state.selectedCats.slice();
      selected.push(i);
      console.log('hi')
      this.setState({ selectedCats: selected, score: this.state.score + 1 })
      if (this.state.score >= this.state.maxScore) this.setState({ maxScore: this.state.score + 1 })
      const newGrid = this.shuffle(this.state.squares);
      this.setState({ squares: newGrid });
    }
    else this.restartGame();


  }

  restartGame() {
    const newGrid = this.shuffle(this.state.squares);
    this.setState({ squares: newGrid, score: 0, selectedCats: [] });
  }

  renderSquare(i) {
    return (
      <Square
        value={this.state.squares[i]}
        onClick={() => this.handleClick(i)}
      />
    );
  }

  render() {
    const status = 'Current Score:';
    const status2 = ' Highest Score:';

    return (
      <div>
        <div className="status">{status}{this.state.score}{status2}{this.state.maxScore}</div>
        <div className="board-row">
          {this.renderSquare(0)}{this.renderSquare(1)}{this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}{this.renderSquare(4)}{this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}{this.renderSquare(7)}{this.renderSquare(8)}
        </div>
        <div className="board-row">
          {this.renderSquare(9)}{this.renderSquare(10)}{this.renderSquare(11)}
        </div>
      </div>
    );
  }
}

class App extends React.Component {
  render() {
    return (
      <div className="game">
        <Board />
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<App />, document.getElementById("root"));

export default App;



